body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
    background: #f0f0f0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

.content-wrapper {
    min-height: 100vh;
}

.search-button {
    position: relative;
    top: 3px;
    cursor: pointer;
}

.search-button:focus {
    outline: none;
}

.search-button .primary-fill {
    fill: #333;
}

.search-button:focus .primary-fill,
.search-button:hover .primary-fill {
    fill: #048ba8;
}

.page-wrapper {
    position: relative;
    top: 72px;
    padding-bottom: calc(72px + 4rem);
    box-sizing: border-box;
}

.page-card {
    top: -200px;
    margin-bottom: -150px;
    z-index: 350;
}

.page-form {
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
}

.details-container > div {
    max-width: 700px;
    margin: 8px auto;
    color: rgba(0, 0, 0, 0.75);
    font-size: 21px;
    font-weight: 300;
    letter-spacing: -0.5px;
    line-height: 28px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

.details-container > div > div {
    display: flex;
    width: 100%;
    padding-bottom: 8px;
    border-bottom: 1px solid #f0f0f0;
}

.details-container > div:last-child > div {
    border-bottom: 0;
}

.image-cards-card-body {
    padding: 10px 10px 40px;
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    align-items: start;
    justify-content: space-between;
}

.image-cards-card-wrapper {
    margin: 8px 0;
    flex-basis: calc(50% - 8px);
}

.image-cards-card-wrapper.featured {
    flex-basis: 100%;
}

.featured-resource {
    border-left: 25px solid #acc35c;
    padding: 28px;
}

.featured-resource p {
    margin: 0 !important;
}

.featured-resource p:first-of-type {
    margin-bottom: 8px !important;
}

.title-icon-header {
    width: 100%;
    display: flex;
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
}

.title-icon-header img {
    width: 32px;
    height: 32px;
}

.center-button button {
    margin: 0 auto;
}

.audio-bar {
    width: 100%;
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
}

.audio-bar audio {
    width: 100%;
}

.attachment-row {
    display: flex;
    flex-wrap: wrap;
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
}

@media screen and (max-width: 767px) {
    .image-cards-card-body {
        display: block;
    }
}

@media screen and (max-width: 968px) {
    .page-card {
        top: -90px;
        margin-bottom: -90px;
    }
}
.login-bar {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 2001;
    background: #333;
    color: #fff;
    font-size: 12px;
    padding: 8px 14px;
    box-sizing: border-box;
    height: 33px;
    display: flex;
    align-items: center;
}

.login-bar a {
    color: #fff;
    margin-left: 16px;
}

header {
    top: 33px;
}
footer {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    width: 100%;
    display: block;
    overflow: hidden;
    margin: 0 auto;
    background: #fff;
    color: #333;
    border-top: 2px solid #acc35c;
}

footer ul {
    padding: 0;
    margin: 0;
}

.footer-container {
    font-size: 13px;
    display: flex;
    width: 100%;
    box-sizing: border-box;
    padding: 50px 60px;
    flex-wrap: wrap;
}

.footer-container .link {
    font-size: 13px;
}

.footer-container ul,
.footer-container p {
    margin-top: 8px;
}

.footer-container ul {
    display: block;
    text-align: center;
}

.footer-container ul li {
    display: inline-block;
    margin: 0 8px;
}

.social-link {
    color: #333;
}

.social-link i {
    margin-right: 8px;
}

@media screen and (max-width: 968px) {
    .footer-container {
        padding: 50px 1rem;
    }

    .footer-column {
        width: 100%;
    }

    .footer-column p,
    .footer-column ul {
        margin-top: 12px;
    }

    .footer-column p {
        text-align: center;
    }

    .footer-mobile-break {
        display: block;
    }

    .footer-mobile-hide {
        display: none;
    }
}
.home-jumbo-wrapper {
    position: relative;
}

.home-jumbo {
    width: 100%;
    overflow: hidden;
    height: 70vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.home-jumbo img {
    min-height: 100%;
    min-width: 100%;
    object-fit: cover;
    object-position: center;
    position: relative;
    flex-shrink: 0;
}

.home-jumbo-meta {
    width: 100%;
    position: absolute;
    top: 220px;
}

.home-jumbo-meta h1,
.home-jumbo-meta p {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    color: #fff;
    text-align: center;
    margin-top: 0;
}

.home-secondary-jumbo-meta {
    position: absolute;
    margin: 50px;
    top: 190px;
}

.home-secondary-jumbo-meta h3,
.home-secondary-jumbo-meta p {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    margin-top: 0;
    color: #fff;
}

.home-secondary-jumbo-meta p {
    color: #fff;
    font-size: 21px;
    font-weight: 300;
    letter-spacing: -0.5px;
    line-height: 28px;
    margin-bottom: 24px;
    margin-left: auto;
    margin-right: auto;
    max-width: 700px;
}

.home-jumbo-meta p {
    font-size: 18px;
    font-weight: 300;
}

.home-jumbo-cta a {
    margin: 0 auto;
}

.home-content {
    padding: 53px 33px 57px;
    width: 100%;
    box-sizing: border-box;
}

.home-content p {
    color: rgba(0, 0, 0, 0.75);
    font-size: 21px;
    font-weight: 300;
    letter-spacing: -0.5px;
    line-height: 28px;
    margin-bottom: 24px;
    margin-left: auto;
    margin-right: auto;
    max-width: 700px;
}

.home-secondary-jumbo-wrapper {
    position: relative;
    box-sizing: border-box;
    overflow: hidden;
    background: #000;
}

.home-secondary-jumbo {
    width: 100%;
    overflow: hidden;
    height: 70vh;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0.5;
}

.home-secondary-jumbo span {
    width: 100%;
}

.home-secondary-jumbo img {
    min-height: 100%;
    min-width: 100%;
    object-fit: cover;
    object-position: center;
    position: relative;
    flex-shrink: 0;
    right: 0;
}

.home-secondary-jumbo-cta a {
    margin: 0;
}

.home-card-row a {
    flex: 1 1;
}

@media screen and (max-width: 1024px) {
    .home-content p {
        font-size: 19px;
        letter-spacing: -0.42px;
        line-height: 25px;
    }
}

@media screen and (max-width: 768px) {
    .home-jumbo {
        height: 90vh;
    }

    .home-jumbo-meta {
        top: 140px;
    }

    .home-secondary-jumbo-meta {
        top: 90px;
        margin: 20px;
    }
}

@media screen and (max-width: 522px) {
    .home-secondary-jumbo img {
        right: 500px;
    }
}

.submit-button {
    margin-top: 32px;
    display: block;
}

.submit-button button {
    margin-left: auto;
    margin-right: auto;
}

.child-input {
    display: flex;
}

.child-input > div:first-child {
    width: calc(75% - 8px);
    margin-right: 8px;
}

.child-input > div:last-child {
    width: 25%;
}

.location {
    text-align: center;
}

.dynamic-form {
    max-width: 700px;
    margin: 4rem auto 0;
}

.messages-card-body {
    padding: 10px 10px 40px;
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    align-items: start;
    justify-content: space-between;
}

.messages-image-card-wrapper {
    margin: 8px 0;
    flex-basis: calc(50% - 8px);
}

.messages-load-more button {
    margin-left: auto;
    margin-right: auto;
}

@media screen and (max-width: 767px) {
    .messages-card-body {
        display: block;
    }
}

.video-container {
    width: 100%;
    overflow: hidden;
    height: 56vw;
    display: flex;
    box-sizing: border-box;
    margin-top: 72px;
    position: relative;
    justify-content: center;
}

.resource-video-container {
    width: 100%;
    overflow: hidden;
    height: 0;
    padding-bottom: 56%;
    display: flex;
    box-sizing: border-box;
    position: relative;
    justify-content: center;
}

.video-container .video-play-icon-button {
    z-index: 1000;
    position: absolute;
    top: 0;
    width: 120px;
    height: 120px;
    display: block;
    margin: calc((56vw / 2) - 60px) auto 0;
    cursor: pointer;
}

.video-container .video-play-icon-button svg {
    fill: #fff;
    width: 100%;
    height: 100%;
}

.video-container iframe,
.resource-video-container iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.video-page-card-wrapper {
    position: relative;
    transition: all 0.3s ease-in-out;
    top: -20vw;
    margin-bottom: 14px;
}

.video-page-card-wrapper.playing {
    top: 0;
    transition: all 0.3s ease-in-out;
    margin-bottom: 200px;
}

.video-page-card-wrapper .page-card {
    top: 0;
}

.video-series-meta {
    max-width: 700px;
    margin-right: auto;
    margin-left: auto;
}

.video-meta p {
    margin-bottom: 0 !important;
}

.video-meta p:last-child {
    margin-top: 0 !important;
}

.video-image-blur {
    width: 100%;
    display: block;
    position: absolute;
    top: 0;
    z-index: 200;
    height: 100%;
}

.video-image-blur img,
.video-image-blur span {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    object-fit: cover;
}

.video-scrollable {
    position: absolute;
    z-index: 199;
    background: transparent;
    display: block;
    bottom: 65px;
    top: 0;
    width: 100%;
}

.video-scrollable.top {
    top: 0;
    height: 20vw;
    width: 100%;
    left: 0;
}

.video-scrollable.bottom {
    bottom: 65px;
    height: 20vw;
    width: 100%;
    left: 0;
}

.video-scrollable.left {
    bottom: 65px;
    top: 0;
    width: 30%;
    left: 0;
}

.video-scrollable.right {
    bottom: 65px;
    top: 0;
    width: 30%;
    right: 0;
}

@media screen and (max-width: 968px) {
    .video-scrollable.bottom {
        height: 10vw;
    }

    .video-page-card-wrapper {
        margin-bottom: 50px;
    }
}

@media screen and (max-width: 640px) {
    .video-container .video-play-icon-button {
        width: 60px;
        height: 60px;
        margin: 3rem auto 0;
    }
}

.song-card {
    padding: 14px;
    margin: 8px 0;
    display: flex;
    align-items: center;
}

.song-meta .artist {
    font-size: 16px !important;
}

.song-meta p {
    margin: 0 !important;
}

.song-play-btn {
    width: 50px;
    height: 50px;
    background: #fff;
    border-radius: 50%;
    margin-left: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: rgba(0, 0, 0, 0.21) 0 3px 3px 0;
}

.song-play-btn svg *:first-child {
    fill: #111;
    transition: fill 0.2s ease-out;
}

.song-play-btn:hover svg *:first-child {
    fill: #048ba8;
}

.enhanced-series-attachments {
    display: flex;
    flex-wrap: wrap;
}

.podcast-card {
    padding: 28px;
    display: flex;
    flex-direction: column;
    min-height: 300px;
}

.podcast-card h4,
.podcast-card p {
    color: #000;
}

.progress-row {
    width: 100%;
    display: flex;
    align-items: flex-start;
    padding-top: 48px;
    position: relative;
}

.progress-row:not(:last-child)::before {
    content: '';
    width: 3px;
    height: 100%;
    display: block;
    background: #AEB2C0;
    position: absolute;
    left: 8px;
    z-index: -1;
    top: 55px;
}


.progress-row.complete::before {
    background: #acc35c;
}

.progress-row:first-child {
    margin-top: 0;
}

.progress-row h3 {
    margin: 0;
}

.progress-row a {
    margin-left: 0;
}

.progress-circle {
    display: block;
    width: 20px;
    height: 20px;
    background: #fff;
    border-radius: 50%;
    border: 3px solid #626E85;
    box-sizing: border-box;
    z-index: 2;
    margin-top: 6px;
    flex-shrink: 0;
}

.active > .progress-circle {
    border: 5px solid #048ba8;
}

.complete > .progress-circle {
    background: #acc35c;
    border-color: #acc35c;
}

.progress-line {
    background: #AEB2C0;
}

.progress-section {
    margin-left: 16px;
}

@media screen and (min-width: 1024px) {
    .progress-tree {
        max-width: 700px;
        margin-left: auto;
        margin-right: auto;
    }
}

.progress-tree {
    width: 100%;
    height: 100%;
    display: block;
    position: relative;
    overflow: hidden;
}

.not-found-wrapper {
    overflow: hidden;
    box-sizing: border-box;
    position: relative;
    padding: 120px 28px 100px;
}

.not-found-wrapper h1 {
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    display: block;
    width: 95%;
}

.not-found-wrapper h1::before {
    content: "";
    width: 14px;
    height: 100%;
    position: absolute;
    background: #acc35c;
    display: block;
    left: -33px;
}

